	module.exports = {"data":{"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"I'm Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}},"markdownRemark":{"html":"<p>In 2017, I had the pleasure to be part of a team in it's very early stages and had the chance to shape the future. I had an idea of how things should be from previous expierences, but I didn't lose an opportunity to question my assumptions in every step we took. That helped me organize better in my head, my knowledge about setting up a development team from scratch.</p>\n<p>In this post, I will like to lay down this process, decissions you will have to make along the way and your options. As in 2018, I will follow this guide if the opportunity shows up again and I will guide any fellow that want's to learn more about the process here.</p>\n<h2 id=\"infrastructure\"><a href=\"#infrastructure\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Infrastructure</h2>\n<p>Hosting service/(VM|P|C)aaS/Serverless/</p>\n<p>Lay down a plan that will take in consideration the business model and shape the infrastructure from the very beggining all the way to the target goal.</p>\n<p>For example, you may think that it would be best to set up a kubernetes cluster with container and enable blue/green deployments that will be reviewed through Jenkins. The wisest decision you could take is start up by setting up much simpler infrastructure but set up a plan that will take you there step by step over the next few months or even years. Analyze the tradeoffs, check how fast you must be there and act accordingly.</p>\n<h2 id=\"source-controlgit-flow\"><a href=\"#source-controlgit-flow\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Source control/Git Flow</h2>\n<p>Git flow, brancing, PRs/Code reviews, Releases, Tags,</p>\n<h2 id=\"automation\"><a href=\"#automation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Automation</h2>\n<p>My first coding mentor once said to me, the third time you will have to do something, you should that it's time to automate it. Not earlier, not later. Ofcourse 3 it's not some kind of a rule, but it's my personal preference and it has worked for me quite well all these years. Sometimes automation may take even more time than to do the actual task so consider the actual gain from the automation.</p>\n<h2 id=\"development-process\"><a href=\"#development-process\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Development Process</h2>\n<p>Agile, Scrum, Meetings, Teams/Leaders,</p>\n<h2 id=\"documentation\"><a href=\"#documentation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Documentation</h2>","excerpt":"In 2017, I had the pleasure to be part of a team in it's very early stages and had the chance to shape the future. I had an idea of how…","frontmatter":{"date":"January 06, 2018","path":"/drafts/setting-up-development-team/","tags":null,"title":"Setting up a development team","draft":true}}},"pathContext":{"prev":false,"next":{"excerpt":"Define the problem ask and seek for answers Know your options research the hell out Execute and measure Let's have a word on premature optimization, on other words, over engineering. The problem with premature optimization is when you are trying to…","html":"<h2 id=\"define-the-problem\"><a href=\"#define-the-problem\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Define the problem</h2>\n<p>ask and seek for answers</p>\n<h2 id=\"know-your-options\"><a href=\"#know-your-options\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Know your options</h2>\n<p>research the hell out</p>\n<h2 id=\"execute-and-measure\"><a href=\"#execute-and-measure\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Execute and measure</h2>\n<p>Let's have a word on premature optimization, on other words, over engineering.</p>\n<p>The problem with premature optimization is when you are trying to forecast the future. When being biased by what you believe will happen when in reality the requirements are quite different. As a result, you end up spending time on something that is merely going to be needed and you could just spend less time producing the same outcome.</p>\n<p>Consider the requirement:</p>\n<blockquote>\n<p>We need an API to support our current 100 customers.</p>\n</blockquote>\n<p>And now this:</p>\n<blockquote>\n<p>We need an API to support our current 100 customers but also 1000 customers that we expect to have at the end of the year based on our customer aquisition report and our current marketing strategy.</p>\n</blockquote>\n<p>And also this:</p>\n<blockquote>\n<p>We need an API to support our current 100 customers plus another 900 customers that may come in the future.</p>\n</blockquote>\n<p>Yes, a Kubernetes cluster built on AWS will serve the requirements quite good. But so a DigitalOcean VM with a Node.js server will do.</p>\n<p>As engineers, we tend to look for the absolute perfect solution on every subject. Quite a few times, that's not the matter. Instead of setting that Kubernetes cluster, spend half of the time to setup the smallest VM and spend the rest of the time to the rest of the backlog.</p>\n<p>Think wisely and most importantly, think in the long term. Can you be sure that the thing you spend most of your time on will best serve your interests five years from now? How can you spend your time on something that will allow you to have more free time in the long term?</p>\n<p>What was the biggest premature optimization you did in your career?</p>\n<p><strong>Did you know about the common <a href=\"https://hackernoon.com/cognitive-biases-in-programming-5e937707c27b\">cognitive biases of programmers?</a></strong></p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/src/pages/a-word-on-over-engineering/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2017-12-02","path":"/drafts/a-word-on-over-engineering/","tags":null,"title":"A word on premature optimization","draft":true}}}}

